.register {
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(
        rgba(255, 255, 255, 0.5),
        rgba(255, 255, 255, 0.5)
      ),
      url("https://images.pexels.com/photos/317355/pexels-photo-317355.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    background-size: cover;
  }
  
  .registerTitle{
      font-size: 50px;
  }
  
  .registerForm {
      margin-top: 20px;
      display: flex;
      flex-direction: column;
  }
  
  .registerForm > label{
      margin: 10px 0;
  }
  
  .registerInput{
      padding: 10px;
      background-color: #fff;
      border: none;
  }
  
  .registerButton{
      margin-top: 20px;
      cursor: pointer;
      background-color: teal;
      border: none;
      color: #fff;
      border-radius:10px;
      padding: 10px;
  }
  
  .registerLoginButton{
      position: absolute;
      top: 60px;
      right: 20px;
      background-color: lightcoral;
      cursor: pointer;
      border: none;
      border-radius: 10px;
      padding: 10px;
      color: #fff;
  }

